import React, { Component } from 'react';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import FaceRecognition from './components/FaceRecognition/FaceRecognition';
import ResultBox from './components/ResultBox/ResultBox';
import Particles from 'react-particles-js';
import Clarifai from 'clarifai'
import './App.css';

const app = new Clarifai.App({
 apiKey: 'd8c1790910954cc8b80342d5e450052a'
});

const particlesOptions = {
  particles: {
    value: 30,
    density: {
      enable:true,
      value_area: 800
    }
	}
};

class App extends Component {
  constructor(){
    super();
    this.state = {
      input: '',
      imageUrl: '',
      celebName: ''
    }
  }

  onInputChange = (event) => {
    this.setState({input: event.target.value, celebName: ''});
  }

  drawCeleb = (data) => {
    const celeb1 = data.outputs[0].data.regions[0]
    .data.face.identity.concepts[0]


    const resultName = "Most likely match: " + celeb1.name;
    console.log(resultName);
    this.setState({celebName: resultName});
  }

  brokenLink = () => {
    this.setState({celebName: "Bad link, try a different one"});
  }

  onSubmit = () => {
    console.log('click');
    this.setState({imageUrl: this.state.input})
    app.models.predict(
      "e466caa0619f444ab97497640cefc4dc",
    this.state.input)
    .then(response => this.drawCeleb(response))
    .catch(err => this.brokenLink());
  }

  render() {
    return (
      <div className="App">
        <Particles className='particles'
          params={particlesOptions}
        />
        <ImageLinkForm
          onInputChange={this.onInputChange}
          onSubmit={this.onSubmit}
        />
        <ResultBox celebName={this.state.celebName}/>
        <FaceRecognition imageUrl={this.state.imageUrl} />
      </div>
    );
  }
}

export default App;



