import React from 'react';

const FaceRecognition = ({imageUrl}) => {
  return(
    <div className="center ma">
      <div className="absolute mt2 pa2">
        <img alt='' src={imageUrl}  width='600px' height='auto'/>
      </div>
    </div>
  );
}


export default FaceRecognition;



