import React from 'react';
import './ImageLinkForm.css';
import {Button, FormControl,Grid,Row,Col} from 'react-bootstrap';

const ImageLinkForm = ({onInputChange, onSubmit}) => {
  return(
    <div className="mainForm">
      <p className="f2">
        {'I will try my best to detect celebrities!'}

      </p>
      <p className="f4">
        {'Paste image URL below'}

      </p>

        <div className="center pa4 br3 shadow-5 w-80 ">

            <div className="cf ph2-ns w-100">
              <div className="fl w-100 w-80-ns pv2">
                  <input className="w-100 f4 pa2 " type='text' onChange={onInputChange}></input>
              </div>
              <div className="fl w-100 w-20-ns pv2">
                  <Button className="w-100 grow f4 link pa2 ph3 dib white bg-light-purple" onClick={onSubmit}>Detect</Button>
              </div>
            </div>
        </div>




    </div>
  );
}
export default ImageLinkForm;



